@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 35px;
  margin: 0 auto;
  padding: 40px 0;
}
.card {
  position: relative;
  width: 100%;
  height: 250px;
  margin: 0 auto;
  background: #1775ee;
  border-radius: 15px;
  box-shadow: 0 15px 60px rgba(0, 0, 0, 0.5);
}
.face {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.face1 {
  box-sizing: border-box;
  padding: 20px;
}
h2 {
  margin: 0;
  padding: 0;
}
.face2 {
  transition: 0.5s;
}
.h2 {
  margin: 0;
  padding: 0;
  font-size: 10em;
  color: #fff;
  transition: 0.5s;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.card:hover .face.face2 {
  height: 60px;
}
h2 {
  font-size: 2em;
}

.card:nth-child(1) .face.face2 {
  background-image: linear-gradient(40deg, #fff 0%, #1775ee 100%);
  border-radius: 15px;
}
.overview {
  position: relative;
  color: #fff;
  margin-top: 0%;
}

.Recommend {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
  text-decoration: underline;
  font-weight: bold;
  font-size: small;
  cursor: pointer;
}
