@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
.topbarContainer {
  height: 50px;
  width: 100%;
  background-color: #1877f2;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarLeft {
  flex: 3 1;
}

.logo {
  font-size: 24px;
  margin-left: 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
}

.topbarCentre {
  flex: 5 1;
}

.searchbar {
  width: 100%;
  height: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

.searchIcon {
  font-size: 20px !important;
  margin-left: 10px;
}

.searchInput {
  border: none;
  width: 70%;
}

.searchInput :focus {
  outline: none;
}

.topbarRight {
  flex: 4 1;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}

.topbarLink {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
}

.topbarIcons {
  display: flex;
  cursor: pointer;
}

.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge {
  height: 15px;
  width: 15px;
  background-color: red;
  border-radius: 50px;
  color: white;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.topbarImg {
  height: 32px;
  width: 32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.filterTitle {
  padding-top: 5%;
}

.filterInput {
  width: 100%;
  text-align: center;
  position: relative;
  height: 30px;
  margin-top: 2%;
}

.submitButton {
  margin-top: 5%;
  height: 30px;
  width: 30%;
  background-color: #1775ee;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
}

.submitButt {
  text-align: center;
  font-size: 24px;
  cursor: pointer;
}
.f8 {
  color: gray;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.sidebar {
    flex: 2 1;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
    font-weight: bold;
    color: #1775ee;
}

.filters {
    text-align: center;
    font-size: large;
    text-shadow: #1877f2;
}

.sidebarWrapper {
    padding: 20px;
}

.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}


.sidebarHr {
    margin: 20px 0;
}
.feed {
    flex: 5.5 1;
}

.feedWrapper {
    padding: 20px;
}
.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 35px;
  margin: 0 auto;
  padding: 40px 0;
}
.card {
  position: relative;
  width: 100%;
  height: 250px;
  margin: 0 auto;
  background: #1775ee;
  border-radius: 15px;
  box-shadow: 0 15px 60px rgba(0, 0, 0, 0.5);
}
.face {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.face1 {
  box-sizing: border-box;
  padding: 20px;
}
h2 {
  margin: 0;
  padding: 0;
}
.face2 {
  transition: 0.5s;
}
.h2 {
  margin: 0;
  padding: 0;
  font-size: 10em;
  color: #fff;
  transition: 0.5s;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.card:hover .face.face2 {
  height: 60px;
}
h2 {
  font-size: 2em;
}

.card:nth-child(1) .face.face2 {
  background-image: linear-gradient(40deg, #fff 0%, #1775ee 100%);
  border-radius: 15px;
}
.overview {
  position: relative;
  color: #fff;
  margin-top: 0%;
}

.Recommend {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
  text-decoration: underline;
  font-weight: bold;
  font-size: small;
  cursor: pointer;
}

.homeContainer {
    display: flex;
    width: 100%;
}
.login {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
}

.loginLeft , .loginRight {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginLogo {
    font-size: 50px;
    font-weight: 800;
    color: #1775ee;
    margin-bottom: 10px;
}

.loginLogo1 {
    font-size: 30px;
    font-weight: 800;
    color: #1775ee;
    margin-bottom: 10px;
}

.loginDesc {
    font-size: 24px;
}

.loginBox {
    height: 300px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.loginInput {
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}

.loginInput:focus {
    outline: none;
}

.loginButton {
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #1775ee;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

.loginForgot {
